import { Card, CardContent, CardActions } from "@mui/material";
import { TextField, Tooltip, Typography, Button } from "@mui/material";
import { Container, Stack } from "@mui/material";
import React from "react";
import ReCAPTCHA from "react-google-recaptcha";


class CreateBox extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            captchaCode: null,
            title: '',
            password: '',
            email: '',
        }
    }
    submit() {
        if (this.state.password === '') {
            alert('Please enter a password')
            return
        }
        if (this.state.captchaCode && this.state.captchaCode !== '') {
            console.log(`${this.state.title} ${this.state.password} ${this.state.email} ${this.state.captchaCode}`)
            fetch(process.env.REACT_APP_API_BASE +'/create-box', {
                mode: 'cors',
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    title: this.state.title==='' ? null : this.state.title,
                    password: this.state.password==='' ? null : this.state.password,
                    email: this.state.email==='' ? null : this.state.email,
                    captchaCode: this.state.captchaCode
                })
            }).then(res => {
                if (res.status === 201) {
                    var text = res.text()
                    alert('Box created!')
                    text.then((res) => {
                        window.location.href = '#view:'+res
                        //reload
                        window.location.reload()
                    })
                } else {
                    alert('Error creating box. Please try again.')
                }
            })
        } else {
            alert('Please verify that you are not a robot.')
        }
    }
    render() {
        return (
            <div style={{ minHeight: '100vh', display: 'flex', alignItems: 'center' }}>
                <Container maxWidth="sm">
                    <Card sx={{ minWidth: 275 }}>

                        <CardContent>
                            <Typography variant="h3" component="h1" color = 'darkblue'>
                                Create a Box
                            </Typography>
                            <Stack spacing={2}>
                                <Tooltip title="The Box's title your friends will see" arrow>
                                    <TextField label="Box Title" id="boxTitle"
                                        value={this.state.title}
                                        variant="standard"
                                        autoComplete="off"
                                        onChange={(e) => this.setState({ title: document.getElementById("boxTitle").value })}
                                    />
                                </Tooltip>
                                <Tooltip title="The Box's password, used for answering questions inside." arrow>
                                    <TextField label="Box Password" id="boxPassword"
                                        type='password'
                                        value={this.state.password}
                                        variant="standard"
                                        autoComplete="off"
                                        onChange={(e) => this.setState({ password:  document.getElementById("boxPassword").value })}
                                        required />
                                </Tooltip>
                                <Tooltip title="Enter an email if you want to be notified when somebody asks you a question" arrow>
                                    <TextField label="Email" id="boxEmail"
                                        type='email'
                                        value={this.state.email}
                                        variant="standard"
                                        onChange={(e) => this.setState({ email: document.getElementById("boxEmail").value})}
                                        required />
                                </Tooltip>
                                <ReCAPTCHA
                                    sitekey={process.env.REACT_APP_SITE_KEY}
                                    onChange={(value) => this.setState({ captchaCode: value })}
                                />
                            </Stack>
                        </CardContent>
                        <CardActions>
                            <Button onClick={()=>this.submit()}>Create Box!</Button>
                        </CardActions>

                    </Card>
                </Container>
            </div>
        )
    }
}

export default CreateBox;