import { Container, Card, CardContent, TextField, Stack, Button, CardActions, Divider, AppBar, Tooltip, Box } from '@mui/material'
import SearchIcon from '@mui/icons-material/Search';
import React from 'react'
import UnAnsweredQuestions from './unAnswered'
import AnsweredQuestions from './Answered'
import Response from './response'
import { textAlign } from '@mui/system';


var questionList = [];
class ViewInner extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            displayData: {},
            questionValue: '',
            email: '',
            password: '',
            showQuestions: false,
            showAnswered: false,
            answerList: [],
            searchPhrase: '',
            searchResults: [],
        }


    }
    componentDidMount() {
        fetch(process.env.REACT_APP_API_BASE + '/box/' + this.props.route, {
            mode: 'cors',
        }).then(res => {
            if (res.status === 200) {
                return res.json()
            } else {

                throw new Error('Error getting box')
            }
        }).then(json => { this.setState({ displayData: json }); }).catch(err => alert(err))

    }
    sendQuestion() {
        if (this.state.questionValue === '') {
            alert('Question cannot be empty!')
            return
        }
        fetch(process.env.REACT_APP_API_BASE + '/ask/' + this.props.route, {
            mode: 'cors',
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                question: this.state.questionValue === '' ? null : this.state.questionValue,
                email: this.state.email === '' ? null : this.state.email,
            })
        }).then(res => {
            if (res.status === 201) {
                alert("Question sent successfully!")
                //reload    
                window.location.reload()
            } else {
                alert('Error creating box. Please try again.')
            }
        })
    }

    moveToCreateBoxPage() {
        window.location.href = '#create-box'
        window.location.reload()
    }

    needPassword() {
        var password = prompt("Please enter the password to view this box");
        password = btoa(password);
        fetch(process.env.REACT_APP_API_BASE + '/unanswered-questions/' + window.location.href.split(':')[window.location.href.split('/').length - 1], {
            mode: 'cors',
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Basic ' + password
            },
        }).then(res => {
            if (res.status === 200) {
                var text = res.text()
                text.then(text => {
                    var resContent = JSON.parse(text)
                    
                    if (text === '') {
                        alert('No questions found!')
                    } else {
                        questionList = []
                        this.setState({ showQuestions: true })
                        for (var i = 0; i < resContent.questions.length; i++) {
                            questionList.push(<UnAnsweredQuestions question={resContent.questions[i].question} id={resContent.questions[i]._id} ></UnAnsweredQuestions>)
                        }
                        console.log(questionList)

                    }
                })
                alert('Password accepted!')


            } else {
                alert('Incorrect password!')
            }
        })

    }

    showAnswered() {
        fetch(process.env.REACT_APP_API_BASE + '/box/' + window.location.href.split(':')[window.location.href.split('/').length - 1], {
            mode: 'cors',
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
        }).then(res => {
            var text = res.text()
            console.log(text);
            text.then(text => {
                var resContent = JSON.parse(text)
                // here we store the raw content for searching
                this.dictionary = this.buildDictionary(resContent)
                console.log(resContent)
                if (text === '') {
                    alert('No answers!')
                } else {

                    this.setState({ showAnswered: true })
                    let answerList = []
                    for (var i = 0; i < resContent.questions.length; i++) {
                        answerList.push(<AnsweredQuestions key={i} id={`question-${i}`} question={resContent.questions[i].content} answer={resContent.questions[i].answer}></AnsweredQuestions>)
                    }
                    // do it in an atomic manner
                    this.setState({ answerList: answerList })
                    console.log('Answer list created', answerList)
                }
            })
        })
    }

    buildDictionary(rawJSON) {
        let dictionary = {}
        let followUpSearchStack = []
        for (var i = 0; i < rawJSON.questions.length; i++) {
            dictionary[rawJSON.questions[i].content] = <AnsweredQuestions key={`result-${i}`} id={`search-${i}`} question={rawJSON.questions[i].content} answer={rawJSON.questions[i].answer} />
            if(rawJSON.questions[i].answer.followUps) rawJSON.questions[i].answer.followUps.forEach(followUp => {
                followUpSearchStack.push(followUp)
            })
        }
        while(followUpSearchStack.length > 0) {
            let followUp = followUpSearchStack.pop()
            dictionary[followUp.content] = <Response key={`result-${followUp.id}`} id={`search-${followUp.id}`} response={followUp.content} followUps={followUp.followUps}/>
            if(followUp.followUps) followUp.followUps.forEach(followUp => {
                followUpSearchStack.push(followUp)
            })
        }
        console.log('Dictionary built', dictionary)
        return dictionary
    }

    fastSearch(phrase) {
        let tokens = phrase
        .toLocaleLowerCase() // convert to lowercase
        // now you start to think like Ritchie - prof. E.
        .replaceAll(/[,./]/g, ' ') // replace all non-alphabetic characters with a space
        .split(' ') // split into words (tokens)
        .filter(e=>e!=='') // remove empty tokens

        let results = []
        if(tokens.length === 0) { // small optimization
            return []
        }
        // search the raw content for the phrase
        for (const [key, value] of Object.entries(this.dictionary)) {
            // if the phrase is found in the raw content, return the question
            if (tokens.some(token => key.toLowerCase().includes(token))) {
                results.push(value)
            }
        }
        console.log('Search results: ', results)
        return results
    }


    render() {
        var showQuestion = [];
        if (this.state.showQuestions === true) {
            showQuestion.push(questionList)
        }


        return (
            <div>
                <AppBar position="static">
                    <Button className color="secondary" variant="contained" onClick={this.moveToCreateBoxPage}><span className='shake'>Create My Box</span></Button>
                </AppBar>

                <div style={{ marginTop: '96px' }}>
                    <Container maxWidth="lg">
                        <Stack spacing={2}>
                            <Card>
                                <CardContent>
                                    <Stack spacing={2}>
                                        <span style={{ color: 'darkblue', fontsize: '100px', fontFamily: 'Monospace', fontWeight: "bold", textAlign:"center"}}>
                                            {this.state.displayData.boxTitle}
                                        </span>

                                        <TextField
                                            label="Ask a question anonymously" id="askQuestion"
                                            multiline
                                            rows={4}
                                            value={this.state.questionValue}
                                            onChange={(e) => this.setState({ questionValue: document.getElementById('askQuestion').value })}
                                            required
                                        />
                                        <Tooltip title='Get notified when your question is answered' >
                                            <TextField
                                                label="Email" id="askEmail"

                                                value={this.state.email}
                                                onChange={(e) => this.setState({ email: document.getElementById('askEmail').value })}
                                            />
                                        </Tooltip>
                                    </Stack>
                                </CardContent>
                                <CardActions>
                                    <Button onClick={() => this.sendQuestion()}>Ask</Button>
                                </CardActions>
                            </Card>
                            <Divider />
                            <AppBar position="static" >
                                <Button color="secondary" variant="contained" onClick={() => this.needPassword()}><span className='shake'>See Unanswered Questions</span></Button>
                            </AppBar>
                            <AppBar position="static" >
                                <Button color="secondary" variant="contained" onClick={() => this.showAnswered()}><span className='shake'>See Answered Questions</span></Button>
                            </AppBar>
                            <div id="questions">
                                {showQuestion[0]}
                            </div>
                            {
                                this.state.showAnswered ?
                                    <div id="answered">
                                        <Tooltip title='Search for ' >
                                            <Box sx={{ display: 'flex', alignItems: 'flex-end', maxWidth: '100%'}}>
                                                <SearchIcon sx={{ color: 'action.active', mr: 1, my: 0.5 }} />
                                                <TextField fullWidth value={this.state.searchPhrase} id="input-with-sx" label="Search" variant="standard" onChange={(e)=>{e.target.scrollIntoView();this.setState({searchPhrase: e.target.value, searchResults: this.fastSearch(e.target.value)})}}/>
                                            </Box>
                                        </Tooltip>
                                        {
                                            this.state.searchPhrase !== '' ?
                                                (
                                                    this.state.searchResults.length > 0 ?
                                                        this.state.searchResults:
                                                        <p>No results found.</p>
                                                ):  
                                                <div id="answers">{this.state.answerList}</div>
                                        }
                                    </div> :
                                    null
                            }
                        </Stack>
                    </Container>

                </div>
            </div>
        )
    }
}
export default ViewInner