import { Checkbox } from "@mui/material";
import React, { useDebugValue } from "react";
import "./content.css";
import Response from "./response";

class AnsweredQuestions extends React.Component{
      constructor(props){
          super(props);
          this.state = {
              question: this.props.question,
              answer: this.props.answer,
              responseList: [],
              depth:0
              }
          }
      
      componentDidMount(){
          this.setState({
            question: this.props.question,
            answer: this.props.answer,
          })
          var followups = this.state.answer.followUps
          var responseList = []
          if(followups)
            for (var i=0; i<followups.length; i++){
                  responseList.push(<Response response= {followups[i].content} id = {followups[i].id} followUps={followups[i].followUps} depth={0}/>)
                  console.log(followups[i].id)
            }
          this.setState({
                responseList:responseList
          })
          console.log(this.state.responseList)
      }
      submitResponse = () => {
            alert("submit response")
            var id = this.state.answer.id;
            fetch(process.env.REACT_APP_API_BASE +'/follow-up/'+ id, {
                mode: 'cors',
                headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
                },
                method: 'POST', 
                body: JSON.stringify({
                email: document.getElementById('email'+id).value,
                response: document.getElementById('response'+id).value,
            })}).then(res => {
                if(res.status === 201){
                alert('success')
                window.location.reload()
                //questionList.push(<AnsweredQuestions question={resContent.questions[i].question} id={resContent.questions[i].id} ></AnsweredQuestions>)
                console.log(res.text())
                }
                else
                alert('fail')
            })
        }
        render(){
            return( 
                <div className="answeredQuestion">
                    <h2>
                        {"Qustion: "+this.state.question}
                  </h2> 
                  <h3>
                        {"Answer: "+this.state.answer.content}
                  </h3>
                  <div className="realFormWrapper">
                    <form>
            
                        <div className="inputWrapper">
                            <textarea type="text" id={"response"+this.state.answer.id} name="response" placeholder="response"  className="contentInput"/>
                            <input type="text" id={"email"+this.state.answer.id} name="email" placeholder="email"  className="contentInput"/>
                            </div>
                    </form>
                        <button  value="  ✅  " className="contentSubmitButton btn" onClick={this.submitResponse}>
                            <span> </span>
                        </button>
                    </div>
                    
                  {/*<Typography >*/}
                  {this.state.responseList}
                  {/*</Typography>*/}
                </div>
            )
        }
      
}
export default AnsweredQuestions;
