import React, { useDebugValue } from "react";
import "./content.css";
import ViewInner from "./viewInner";

class Content extends React.Component {
    constructor(props) {
        super(props);
        this.content=React.createRef();
        this.main=React.createRef();
        this.state = {
            title: "",
        }
        this.questions = [];
    }

    //loading function
    loading=()=>{
        this.main.current.innerHTML="Loading...";
    }

    componentDidMount(){
        //get the url
        let urls = window.location.href;
        let urlArray = urls.split("/");
        var id = urlArray[urlArray.length-1];

        this.content.current.style.top="20px";
        this.content.current.style.left="-20px";
        this.content.current.style.transition="0.5s";
        
        setTimeout(() => {
            this.content.current.style.opacity = "1";
            this.content.current.style.transition="1s";
            this.content.current.style.top="50px";
            this.content.current.style.left="10px";

            this.content.current.style.transform="rotateX(40deg) translateZ(50px) translateZ(-100px)";

            setTimeout(() => {
                //stop click event propagation
                this.content.current.style.color="black";   
                this.content.current.style.transition="0.3s";
                this.content.current.style.top="30px";
                this.content.current.style.width="60%";
                this.content.current.style.left="370px";
                this.content.current.style.fontSize = "30px";
                this.content.current.style.transform = "rotateX(0deg) ";
                this.content.current.style.padding = "20px";
                //get height of content
                setTimeout(() => {
                    this.content.current.style.top="30px";
                    this.content.current.style.height="auto"
                }, 100);
            }, 500);
        },200);
    
    }
    


    render() {
        const operations =[];   
        for (let i = 0; i < this.questions.length; i++) {
            operations.push(this.questions[i]);
        }

        return (
            <div id="content" className="content" ref={this.content} onClick={(e)=>{e.stopPropagation()} }>
                <ViewInner route={this.props.route}></ViewInner>

                    
            </div>
        );
    }
}

export default Content;