import React from "react";
import ReactDOM from "react-dom";
import "./mainpage.css";
import Bar from "./bar";
import ViewInner from "./viewInner";
import Content from "./content";

class Face extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return <div className={this.props.className}></div>;
  }
}

class LowPart extends React.Component {
  render() {
    return (
      <div className="lowPart">
        <Face position="down" className="down" />
        <div className="front">{this.props.content}</div>
        <Face position="back" className="back" />
        <Face position="left" className="left" />
        <Face position="right" className="right" />
      </div>
    );
  }
}

class HighPart extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="highPart" style={this.props.style}>
        <Face position="up" className="up" />
        <Face position="upfront" className="upfront" />
        <Face position="upback" className="upback" />
        <Face position="upleft" className="upleft" />
        <Face position="upright" className="upright" />
      </div>
    );
  }
}

class Chest extends React.Component {
  constructor(props) {
    super(props);
    this.upPart = React.createRef();
    this.state = {
      rotate: -30,
    };
  }

  clickAndOpen = () => {
    if (this.state.rotate === -30) {
      this.setState({ rotate: 65 });
      //give a time change the upPart
      this.upPart = 1;
    } else {
      this.setState({ rotate: -30 });
      this.upPart = 0;
    }
  };

  render() {
    var show = [];
    if (this.upPart == 1) {
      show.push(<Content route={this.props.route} />);
    } else {
    }
    return (
      <div className="chest">
        <HighPart
          upPart={this.upPart}
          style={{ transform: "rotateX(" + this.state.rotate + "deg)" }}
        />
        <div onClick={this.clickAndOpen} style={{ height: "50%" }}>
          {show[0]}
          <LowPart />
        </div>
      </div>
    );
  }
}

class LockedChest extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
    };
  }

  render() {
    return (
      <div className="chest " id="lockedchest">
        <HighPart />

        <LowPart content="Enter Your Key" />
      </div>
    );
  }
}

class View extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      route: this.props.route,
    };
  }

  wrapper = React.createRef();

  componentDidMount() {
    this.setState({
      route: this.props.route,
    });
    setTimeout(() => {
      if(window.outerWidth > 1024)this.wrapper.current.style.marginLeft = "100px";
    }, 150);
  }

  render() {
    return (
      <div className="mainPage">
        <Bar />
        <div className="wrapper" ref={this.wrapper}>
          {window.outerWidth < 1024 ? (
            <ViewInner route={this.state.route}></ViewInner>
            
          ) : (
            <Chest route={this.state.route} />
          )}
          
        </div>


      </div>
    );
  }
}

export default View;
