import { Checkbox } from "@mui/material";
import React, { useDebugValue } from "react";
import AnsweredQuestions from "./Answered";
import "./content.css";

class UnAnsweredQuestions extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            question: this.props.question,
            id: this.props.id,
            }
        }
    
    componentDidMount(){
        this.setState({
            id:this.props.id,
            question: this.props.question,
        })
        console.log (1)
    }

    submitAnswer = () => {
        console.log(this.state.id)
        console.log( document.getElementById('private'+this.state.id).value, document.getElementById('answer'+this.state.id).value,)
        fetch(process.env.REACT_APP_API_BASE +'/answer/'+ this.state.id, {
            mode: 'cors',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
            },
            method: 'POST', 
            body: JSON.stringify({
            private: document.getElementById('private'+this.state.id).value,
            answer: document.getElementById('answer'+this.state.id).value,
        })}).then(res => {
            if(res.status === 201){
            alert("Success")
            //questionList.push(<AnsweredQuestions question={resContent.questions[i].question} id={resContent.questions[i]._id} ></AnsweredQuestions>)
            console.log(res.text())
            window.location.reload()
            }
            else
            alert('fail')
        })
    }
        

        render(){
            return( 
                <div className="unAnsweredWrapper">
                    <h2>
                        {"Question: "+this.state.question}
                    </h2> 
                    <div className="realFormWrapper">
                        <form>
                            <div className="inputWrapper">
                                <textarea type="text" id={"answer"+this.state.id} name="answer" placeholder="Answer"  className="contentInput" required></textarea>
                                <span style={{color:"white",display:"block !important"}}>Notified? </span> 
                                <input type="checkbox" id={"private"+this.state.id} style={{zoom:"150%", marginLeft:"20px"}}>
                                </input>
                            </div>
                        </form>
                    <button className="contentSubmitButton btn" onClick={this.submitAnswer}><span></span></button>
                    </div>
                </div>
            )
        }
}

export default UnAnsweredQuestions;