import React from "react";
import "./content.css";

class Response extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: this.props.id,
            response: this.props.response,
            followUps: this.props.followUps?this.props.followUps:[],
            responseList: [],
            followUpsCollapsed: true
            //   depth: this.props.depth+1
        }
    }

    componentDidMount() {
        var followupList = []

        for (var i = 0; i < this.state.followUps.length; i++)
            followupList.push(<Response
                key={this.state.followUps[i].id}
                response={this.state.followUps[i].content}
                id={this.state.followUps[i].id}
                followUps={this.state.followUps[i].followUps}
            />)
        this.setState({ responseList: followupList })
    }

    submitResponse = () => {
        alert('submit')
        fetch(process.env.REACT_APP_API_BASE + '/follow-up/' + this.state.id, {
            mode: 'cors',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            method: 'POST',
            body: JSON.stringify({
                email: document.getElementById('email' + this.state.id).value,
                response: document.getElementById('response' + this.state.id).value,
            })
        }).then(res => {
            if (res.status === 201) {
                alert('success')
                //questionList.push(<AnsweredQuestions question={resContent.questions[i].question} id={resContent.questions[i].id} ></AnsweredQuestions>)
                window.location.reload()
            }
            else
                alert('fail')
        })
    }

    render() {


        return (
            <div className="response">
                <h4>

                    {this.state.response}
                </h4>
                <div className="realFormWrapper">
                    <form>
                        <div className="inputWrapper">
                            <textarea type="text" id={"response" + this.state.id} name="response" placeholder="response" className="contentInput" />
                            <input type="text" id={"email" + this.state.id} name="email" placeholder="email" className="contentInput" />
                        </div>
                    </form>
                    <button className="contentSubmitButton btn" onClick={this.submitResponse}>
                    <span> </span>
                    </button>
                </div>
                <div className="followDiv">
                    <div style={{ fontcolor: 'red' }} >
                        {!this.state.followUpsCollapsed ? this.state.responseList : null}
                    </div>
                </div>
                {
                    this.state.followUps.length > 0 ?
                        <div className="showFollow" onClick={() => this.setState({ followUpsCollapsed: !this.state.followUpsCollapsed })}>
                            {!this.state.followUpsCollapsed ? 'Hide The Follow-ups' : 'Show The Follow-ups'}
                        </div>:
                        null
                }

            </div>
        )
    }

}
export default Response;
