import CreateBox from './routes/createBox'
import Welcome from './routes/welcome'
import View from './routes/view'

const router = {
  'default': <Welcome />,
  'create-box': (routeString) => <CreateBox />,
  'question': (routeString) => <View route={routeString} />,
  'view': (routeString) => <View route={routeString} />,
  'answer': (routeString) => <View route={routeString} />,
}
function App(props) {
  if(!props.location)
    return router.default
  let routeParts = props.location.split(':')
  if (routeParts[0] in router) 
    return router[routeParts[0]](routeParts[1]);
  return router.default
}

export default App;
